<template lang="html">
  <div class="editor">
    <div ref="toolbar" class="toolbar">
    </div>
    <div ref="editor" class="text">
    </div>
  </div>
</template>

<script>

  const defaultContent = `<h2 style="text-align: center;"></h2><p>&nbsp; &nbsp; &nbsp; <span style="font-weight: bold;"></span><span style="font-style: italic;"></span><span style="text-decoration-line: underline;"></span><span style="text-decoration-line: line-through;"></span><span style="color: rgb(249, 150, 59);"></span><span style="background-color: rgb(249, 150, 59);"></span>emoji</p><p><br></p><p></p><ol><li></li><li></li><li></li></ol><p></p><ul><li></li><li></li><li></li></ul><p><a href="http://bing.com/" target="_blank"></a></p><p><br></p><p></p><p></p><p style="text-align: center;"><img src="http://thumb.takefoto.cn/wp-content/uploads/2019/04/201904080619552096-680x448.jpg"></p>`

  import E from 'wangeditor'
  export default {
    name: 'editor',
    data () {
      return {
        editor: null,
        info_: null,
        valued: false
      }
    },
    model: {
      prop: 'value',
      event: 'change'
    },
    props: {
      value: {
        type: String,
        default: ''
      },
      isClear: {
        type: Boolean,
        default: false
      }
    },
    watch: {
      isClear (val) {
        // 
        if (val) {
          this.editor.txt.clear()
          this.info_ = null
        }
      },
      value (val) {
        console.log(val);
        if (val != this.info_) {
          const isEmpty = !(val || '').replace(/<(\/)?[\w\s]+>/g, '')
          this.editor.txt.html(isEmpty ? defaultContent : this.removeWordXml(val))
        }
        this.valued = true
      }
    },
    mounted () {
      this.setEditor()
      this.editor.txt.html(this.value)

      setTimeout(() => {
        const isEmpty = !(this.value || '').replace(/<(\/)?[\w\s]+>/g, '')

        if (!this.valued && isEmpty) {
          this.editor.txt.html(defaultContent)
          this.valued = true
        }
      }, 1000)
    },
    methods: {

      getHtml() {
        console.log(this.editor.txt.html());

      },

      removeWordXml(text){
        let html = text;
        html = html.replace(/<\/?SPANYES[^>]*>/gi, "");//  Remove  all  SPAN  tags
        html = html.replace(/<(\w[^>]*)  class=([^|>]*)([^>]*)/gi, "<$1$3");  //  Remove  Class  attributes
        html = html.replace(/<(\w[^>]*)  style="([^"]*)"([^>]*)/gi, "<$1$3");  //  Remove  Style  attributes
        html = html.replace(/<(\w[^>]*)  lang=([^|>]*)([^>]*)/gi, "<$1$3");//  Remove  Lang  attributes
        html = html.replace(/<\\?\?xml[^>]*>/gi, "");//  Remove  XML  elements  and  declarations
        html = html.replace(/<\/?\w+:[^>]*>/gi, "");//  Remove  Tags  with  XML  namespace  declarations:  <o:p></o:p>
        html = html.replace(/&nbsp;/, "");//  Replace  the  &nbsp;
        html = html.replace(/\n(\n)*( )*(\n)*\n/gi, '\n');
         // Transform  <P>  to  <DIV>
        // let  re  =  new  RegExp("(<P)([^>]*>.*?)(<//P>)","gi")  ;            //  Different  because  of  a  IE  5.0  error
        //    html = html.replace(re, "<div$2</div>");
        this.editor.txt.html(html);
      },
      setEditor () {
        this.editor = new E(this.$refs.toolbar, this.$refs.editor)

        this.editor.customConfig.uploadImgShowBase64 = true // base 64 
        this.editor.customConfig.uploadImgServer = ''// 
        this.editor.customConfig.uploadImgHeaders = {      }//  header
        this.editor.customConfig.uploadFileName = '' // 
        this.editor.customConfig.uploadImgMaxSize = 5 * 1024 * 1024 //  5M
        this.editor.customConfig.uploadImgMaxLength = 10 //  10 
        this.editor.customConfig.uploadImgTimeout = 3 * 60 * 1000 // 
        this.editor.customConfig.pasteFilterStyle = false;
        this.editor.customConfig.fontNames = [
          '',
          '',
          '_GB2312',
          '',
          '',
        ]
        // 
        this.editor.customConfig.menus = [
          'head', // 
          'bold', // 
          'fontSize', // 
          'fontName', // 
          'italic', // 
          'underline', // 
          'strikeThrough', // 
          'foreColor', // 
          'backColor', // 
          'link', // 
          'list', // 
          'justify', // 
          'quote', // 
          'emoticon', // 
          'image', // 
          'table', // 
          'video', // 
          'code', // 
          'undo', // 
          'redo' // 
        ]

        this.editor.customConfig.uploadImgHooks = {
          fail: (xhr, editor, result) => {
            // 
          },
          success: (xhr, editor, result) => {
            // 
          },
          timeout: (xhr, editor) => {
            // 
          },
          error: (xhr, editor) => {
            // 
          },
          customInsert: (insertImg, result, editor) => {
            // 
          }
        }
        this.editor.customConfig.onchange = (html) => {
          this.info_ = html // 
          this.$emit('change', this.info_) // 
        }

        // 
        this.editor.create()
      }
    }
  }
</script>

<style lang="css">
  .editor {
    width: 100%;
    margin: 0 auto;
  }
  .toolbar {
    border: 1px solid #ccc;
  }
  .text {
    border: 1px solid #ccc;
    height: 500px;
  }

  .w-e-toolbar {
    flex-wrap: wrap !important;
  }


  /*.w-e-menu:hover {*/
    /*z-index: 10002 !important;*/
  /*}*/

  .w-e-menu{
    z-index: 2 !important;
  }
  .w-e-text-container{
    z-index: 1 !important;
  }
</style>



